var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',{staticClass:"pa-0 ma-0",attrs:{"fluid":""}},[_c('v-row',{attrs:{"no-gutters":""}},[_c('v-parallax',{staticClass:"start-image",attrs:{"height":"300","src":require("../assets/Title4 copy.png")}},[_c('v-row',{attrs:{"align":"center","justify":"center"}},[_c('v-col',{staticClass:"text-center",attrs:{"cols":"12"}},[_c('h1',{staticClass:"text-h2 font-weight-thin mb-4"},[_vm._v(" KL4AD ")]),_c('h4',{staticClass:"text-h4 subheading"},[_vm._v(" Knowledge-Infused Learning for Autonomous Driving ")]),_c('h5',{staticClass:"text-h5 subheading"},[_vm._v(" ISWC'22 Tutorial ")])])],1)],1)],1),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" Abstract ")])]),_c('v-row',{staticClass:"pa-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('p',{staticClass:"text-justify"},[_c('b',[_vm._v("Autonomous Driving (AD)")]),_vm._v(" is considered as a testbed for tackling many hard AI problems. Despite the recent advancements in the field, AD is still far from achieving full autonomy due to core technical problems inherent in AD. The emerging field of neuro-symbolic AI and the methods for "),_c('b',[_vm._v("knowledge-infused learning")]),_vm._v(" are showing exciting ways of leveraging external knowledge within machine/deep learning solutions, with the potential benefits for interpretability, explainability, robustness, and transferability. In this tutorial, we will examine the use of knowledge-infused learning for three core state-of-the-art technical achievements within the AD domain. With a collaborative team from both academia and industry, we will demonstrate recent innovations using real-world datasets. ")])]),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" Motivation ")])]),_c('v-row',{staticClass:"pa-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('p',{staticClass:"text-justify"},[_vm._v(" To realize the vision of autonomous driving, the automotive industry is investing heavily into core AI technologies and deploying fleets of vehicles across the world to collect massive amounts of data, including – but not limited to – video, LIDAR, and RADAR. To manage these vast amounts of data, companies are beginning to experiment with the use of KGs, which are capable of representing meaningful relations between entities in the world while helping to enable the principles of FAIR data – i.e. findability, accessibility, interoperability, and re-use. Simultaneously, current research into the emerging topic of "),_c('b',[_vm._v("knowledge-infused learning [1] -- or more generally, neuro-symbolic AI ")]),_vm._v(", which is often dubbed as the "),_c('b',[_vm._v(" 3rd wave of AI [2]")]),_vm._v(" -- is showing new and exciting uses for KGs in AI applications. Knowledge-infused learning proposes intuitive ways of leveraging knowledge graphs within machine/deep learning solutions. This infusion of knowledge has been shown to improve the predictive capabilities of ML/DL while providing great potential for improving interpretability, explainability, robustness, and transferability. For these reasons, knowledge-infused learning holds much promise for helping to meet the complex technical challenges of autonomous driving (AD). In this tutorial, we will focus on three distinct technical innovations that leverage knowledge-infused learning. ")])]),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" Detailed Description ")])]),_c('v-row',{staticClass:"pa-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('p',{staticClass:"text-justify"},[_vm._v(" We seek to provide an overview of knowledge-infused learning, an examination of its use for autonomous driving, and three deep dives into current state-of-the-art technical achievements. Specifically, the tutorial will consist of 5 interconnected lecture modules; two 30 mins introductory modules followed by three 40 mins modules for each application. The audience is expected to have a basic understanding of deep/machine learning, semantic technologies (e.g., RDF, OWL, linked open data) and semantic embedding approaches. We aim to guide attendees through a high-level tour of the knowledge-infused learning methods, basic familiarity into open-domain autonomous driving datasets/challenges, and the process of solving domain problems using neuro-symbolic AI methods. We'll be covering the following modules in this tutorial. ")]),_c('ul',[_c('li',[_c('b',[_vm._v("Knowledge-infused Learning (KL): ")]),_vm._v(" KL a form of neuro-symbolic AI is a novel paradigm that seeks to incorporate a variety of explicit (symbolic) knowledge into a data-driven statistical AI framework that supports advancement in machine intelligence. Utilizing knowledge and data in deep learning models to enable learning from lower-level syntactic and lexical features from data through statistical (deep) learning as well as higher-level concepts from knowledge. Using knowledge also allows greater transparency in the decision-making, and enables explanations that users need for informed decision making. Recently, researchers have categorized the recent approaches into a continuum that comprises of three stages; namely, shallow, semi-deep, and deep infusion. We will provide a detailed overview for each stage of knowledge infusion with examples across several application domains. ("),_c('i',[_vm._v("See relevant resources:")]),_vm._v(" [1])")]),_c('br'),_c('li',[_c('b',[_vm._v("Applications for Autonomous Driving: ")]),_vm._v(" Despite recent advancements, AD is still far from ready to meet full level-5 autonomy requirements. The primary obstacle is the "),_c('i',[_vm._v("open-world")]),_vm._v(" environment in which the autonomous vehicle must operate. Navigating this environment requires making informed decisions and taking actions within novel and complex situations. While machine learning, in general, and autonomous driving, in particular, have had much success in "),_c('i',[_vm._v("closed-world")]),_vm._v(" environments with lots of experiential training data, the challenges posed by an open-world environment are yet to be resolved. Additionally, the full autonomy of self-driving and its widespread usage poses a new set of non-technical challenges, including user acceptance, accountability, explainability, and conformance to legal, ethical, and societal boundaries. We believe that knowledge-infused learning -- through an integration of domain knowledge and machine learning -- is a potent tool to overcome such challenges. ("),_c('i',[_vm._v("See relevant resources:")]),_vm._v(" [3])")]),_c('br'),_c('li',[_c('b',[_vm._v("Knowledge-based Entity Prediction (KEP) for Improved Machine Perception: ")]),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/kep.png"),"align":"right","width":"330","height":"200"}}),_vm._v(" Machine perception is one of the key technical problems in AD where detecting and recognizing objects/events in a scene (i.e. environmental perception) is a critical task. This, however, is particularly challenging as there are several reasons why an entity could go unrecognized. This includes, but not limited to, sensor failure, occlusion, low resolution (e.g., from weather), or errors in computer vision models. For example, consider the case where an ego vehicle driving through a residential neighborhood and detects only a ball on the road (partial observation) and could contain unrecognized entities such as "),_c('i',[_vm._v("Child")]),_vm._v(" in the scene. Knowledge-based entity prediction (KEP) is a novel task that aims to address this issue by leveraging relational knowledge from heterogeneous sources in predicting potentially unrecognized entities\\cite{wickramarachchi2021knowledge}. We will show how an innovative knowledge-infused learning approach can be developed for KEP and demonstrate its usefulness considering two high-quality, large-scale real AD datasets. ("),_c('i',[_vm._v("See relevant resources:")]),_vm._v(" [4,5])")]),_c('br'),_c('li',[_c('b',[_vm._v("Leveraging Commonsense for Explainable Scene Clustering: ")]),_vm._v(" The task of scene clustering refers to clustering a given set of scenes and assigning descriptive label(s) to each cluster. For example, a cluster may be described as "),_c('i',[_vm._v("accident")]),_vm._v(" scenes with a crashed vehicle, a police car, and an ambulance. Using scenes represented in a knowledge graph, and enriched with commonsense knowledge, it may be possible to automatically generate explanations for semantic clusters. In this tutorial, we will demonstrate how an innovative approach can be developed for explainable scene clustering along with the process of leveraging relevant commonsense knowledge for this task. ("),_c('i',[_vm._v("See relevant resources:")]),_vm._v(" [6])")]),_c('br'),_c('li',[_c('b',[_vm._v("Learning Visual Models for Road Sign Recognition using a Knowledge Graph as a Trainer: ")]),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/road_signs.png"),"align":"right","width":"370","height":"150"}}),_vm._v(" The task of recognizing and understanding road signs is an important feature of autonomous driving. Current technology for road sign recognition (RSR) is mainly based on computer vision (CV) algorithms that solely depend on image data distribution of the training domain. Such models, however, tend to fail when applied to a target domain (e.g., road signs from China) that differs from their source domain (e.g. road signs from Germany) (see Fig. \\ref{fig:roadsigns}). Knowledge graph neural network (KG-NN) is a novel neuro-symbolic approach to address the problem of transfer learning by using image-data-invariant auxiliary knowledge. We will show how this technology can be applied to enable transfer learning for the RSR task. ("),_c('i',[_vm._v("See relevant resources:")]),_vm._v(" [7])")])])]),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" Speakers ")])]),_c('v-row',{staticClass:"pb-8",attrs:{"align":"center","justify":"center","no-gutters":""}},[_vm._l((_vm.authors),function(author,idx){return [_c('biography-card',{key:idx,attrs:{"person":author}})]})],2),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" Tutorial Program ")])]),_c('v-row',{staticClass:"pb-8 pr-8 pl-8",attrs:{"align":"center","justify":"center","no-gutters":""}},[_c('v-timeline',_vm._l((_vm.programItems),function(item,i){return _c('v-timeline-item',{key:i,attrs:{"color":item.color,"small":""},scopedSlots:_vm._u([{key:"opposite",fn:function(){return [_c('span',{class:("headline font-weight-bold " + (item.color) + "--text"),domProps:{"textContent":_vm._s(item.time)}})]},proxy:true}],null,true)},[_c('div',{staticClass:"py-4"},[_c('h2',{class:("headline font-weight-light mb-4 " + (item.color) + "--text")},[_vm._v(" "+_vm._s(item.title)+" ")]),_c('div',[_vm._v(" "+_vm._s(item.description)+" ")])])])}),1)],1),_c('v-row',{staticClass:"pt-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('h1',{staticClass:"text-h4 font-weight-thin mb-4"},[_vm._v(" References ")])]),_c('v-row',{staticClass:"pa-8",attrs:{"no-gutters":"","align":"center","justify":"center"}},[_c('p',{staticClass:"text-justify"},[_c('ol',[_c('li',[_vm._v(" Sheth, A., Gaur, M., Kursuncu, U., Wickramarachchi, R.: Shades of knowledge- infused learning for enhancing deep learning. IEEE Internet Computing (2019)")]),_c('li',[_vm._v("Garcez, A.d., Lamb, L.C.: Neurosymbolic ai: The 3rd wave. arXiv prep